<template>
  <div class="projects-list-item">
      <div class="projects-list-item__img-container">
        <img :src="project.cover" alt="" class="projects-list-item__img">
      </div>
      <div class="projects-list-item__info">
        <h2 class="projects-list-item__name">{{project.name}}</h2>
        <p class="projects-list-item__description">{{project.description}}</p>
        <button class="projects-list-item__button" @click="push(project.link)">View Details</button>
      </div>
  </div>
</template>

<script>
export default {
    props: [
        'project'
    ],
    methods: {
        push(link) {
            this.$router.push('/projects/' + link)
        }
    }
}
</script>

