<template>
  <div class="gallery">
    <div class="gallery__big">
      <img :src="big" alt="" class="gallery__big-img">
    </div>
    <div class="gallery__slider" >
        <div class="gallery__item" v-for="(item, index) in getImages(images, currentIndex)" :key="index">
          <img @click="big = item" :src="item" alt="" class="gallery__img"> 
        </div>
    </div>
    <!-- <div class="gallery__container-big">
      <img :src="big" alt="" class="gallery__big-img">
    </div>
    <div class="gallery__right">
      <button class="gallery__button button_up" @click="prev()">Up</button>
      <div class="gallery__container-small">
        <div class="gallery__slider" >
          <div class="gallery__item-wrapper" v-for="(item, index) in getImages(images, currentIndex)" :key="index">
            <img @click="big = item" :class="opened ? 'opened' : '' " :src="item" alt="" class="gallery__item-small"> 
          </div>
        </div>
      </div>
      <button class="gallery__button button_down" @click="next()">Down</button>
    </div> -->
  </div>
</template>

<script>
export default {
  props: [
    'images'
  ],
  data() {
    return {
      currentIndex: 0,
      transition: false,
      opened: false,
      big: '',
    }
  },
  mounted() {
    this.big = this.images[0]
    // this.getImageHeight()
  },  
  methods: {
    getImages(images, index) {
      return images.slice(index, index+4)
    },
    getSliderHeight() {
      // let
      // let images = document.querySelectorAll('.gallery__item-small')  
      // setTimeout(() => {
      
      //   let container = document.querySelector('.gallery__container-small')
      //   if (container) {
      //     let containerHeight =  this.getHeight(container)     
      //     if (images) {
      //       images.forEach(image => {
      //         image.style.height = containerHeight / 4 + 'px'
      //       })
      //     }
      //   }
      // }, 100);
    },
    open() {
      if (!this.opened) {
        this.opened = true
      } else {
        this.opened = false
      }
      
    },
    getHeight(element){
      let styles = window.getComputedStyle(element)
      return parseInt(styles.height) + parseInt(styles.marginBottom) + parseInt(styles.marginTop)
    },
    next() {
      if (!this.transition) {
        if (this.currentIndex < this.images.length-3) {
          // this.move(-this.getStep())
          this.currentIndex++
          console.log('curr: ' + this.currentIndex)
       }
      }
    },
    prev() {
      if (!this.transition) {
        if (this.currentIndex > 0) {
          // this.move(this.getStep())
          this.currentIndex--
        }
      }
    },
    getStep() {
      let item = window.getComputedStyle(this.$el.querySelector('.gallery__item-small'))
      return parseInt(item.height) + parseInt(item.marginBottom)
    },
    move(step) {
      let slider = this.$el.querySelector('.gallery__slider')
      let styles = window.getComputedStyle(slider)
      this.transition = true
      setTimeout(()=>{
        this.transition = false
      }, 500)
      slider.style.marginTop = parseInt(styles.marginTop) + step +  'px'
    }
  }
}
</script>

